@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf');
}

$font: Montserrat;
$black: #013557;
$white: #fff;
$grad-green-blue: linear-gradient(-45deg, #00ffbf, #1b8efa);
$grad-red-orange: linear-gradient(45deg, #ff3c00, #fab31b);
$selection: #00ffbf;
$light-gray: #999;
$light-white: #f3f3f3;

::selection {
    background-color: $selection;
    color: $black;
}
body {
    font-family: $font;
    background-color: $light-white;
    color: $black;
}
hr {
    border: 1px solid #ededed;
    margin: 10px 0;
}

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}
/* -- Smooth Scrolling -- */

/* Scroll to Top */
#toTop {
    padding: 10px;
    background-image: $grad-green-blue;
    color: $white;
    bottom: 20px;
    right: 10px;
    z-index: 15;
    display: none;
    cursor: pointer;
    position: fixed;
    opacity: .5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=95)";
    border-radius: 50%;
    width: 45px;
    height: 45px;
    text-align: center;
}
    
#toTop:hover {
    opacity: 1;
}
    /* -- Scroll to Top -- */

@media screen and (max-width: 990px) {
    #toTop {
        bottom: 70px;
        right: 20px;
    }
}