@import 'reset';
@import 'global';

body {
    font-family: $font;
    background-color: $light-white;
}

/* Header Contents */
header {
    background-image: $grad-green-blue;
    color: $white;
    text-align: center;
    border-radius: 0;
    
    h1 {
        color: $white;
        small.lead {
            margin-top: 5px;
            font-size: 15px;
            display: block;
        }
    }
}
/*-- Header Contents --*/

nav.navbar {
    background-image: $grad-green-blue;
    font-size: 16px;
    .navbar-toggler {
        background-image: $grad-red-orange;
        border-radius: 0;
    }
    .navbar-collapse {
        justify-content: space-between;
        .current, .current:hover, a:hover {
            background-image: $grad-red-orange;
            color: $white;
        }
    }
}

.d-flex {
    .btn {
        background-image: $grad-red-orange;
        color: $white;
    }
}

/* Social-Media Button */
.social-btn {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    z-index: 10;
    a {
        flex-grow: 1;
        text-align: center;
        padding: 10px;
        color: $white;
        background-image: $grad-red-orange;
        font-size: 20px;
    }
}
/*-- Social-Media Button --*/

/* Article Contents */
article {
    position: relative;
    background-color: $light-white;
}

/* Realtime Datetime dan Caption */
#date-time {
    p {
        padding: 5px 10px;
        background-image: $grad-red-orange;
        font-size: 13px;
        color: $white;
        font-weight: bolder;
        text-align: center;
    }
}

#attention {
    line-height: 25px;
    blockquote {
        border-radius: 40px;
        background-image: $grad-green-blue;
        margin: 20px;
        padding: 20px;
        text-align: center;
        color: $white;
        position: relative;
        font-weight: bold;
    }
    blockquote::before, blockquote::after {
        position: absolute;
        font-size: 50px;
        text-shadow: 0 0 2px $black;
    }
    blockquote::before {
        content: open-quote;
        top: 5px;
        left: 10px;
    }
    blockquote::after {
        content: close-quote;
        bottom: -20px;
        right: 10px;
    }
}
/*-- Realtime Datetime dan Caption --*/

/* Breadcrumb */
.breadcrumb {
    font-size: 13px;
    line-height: 20px;
    background-color: $light-white;
    a {
        color: $white;
        background-image: $grad-green-blue;
        border-radius: 10px;
        padding: 2px 10px;
    }
}
/*-- Breadcrumb --*/

#post {
    .card {
        border: none;
        /* Post Label */
        .badge {
            background-image: $grad-green-blue;
            border-radius: 15px;
            padding: 5px 10px;
            font-weight: normal;
        }
        /*-- Post Label --*/
    }
    img {
        width: 100%;
        border-radius: 5px;
    }
    h2, h3 {
        line-height: 25px;
        margin: 10px 0;
        color: $black;
        font-weight: bolder;
    }
    h2 {
        font-size: 18px;
    }
    h3 {
        font-size: 16px;
        background-image: $grad-green-blue;
        color: $white;
        margin-top: 30px;
        padding: 10px;
        padding-left: 20px;
        border-left: 7px solid $black;
    }
}
#author {
    h2 {
        font-size: 18px;
    }
    h3 {
        font-size: 16px;
    }
}

/* Postingan */
.card-body {
    blockquote {
        line-height: 25px;
        background-image: $grad-green-blue;
        margin: 10px 0;
        padding: 10px;
        padding-left: 35px;
        color: $white;
        position: relative;
        font-weight: 500;
        text-shadow: 0 0 3px $black;
        font-style: oblique;
        font-size: 14px;
        border-radius: 10px;
    }
    blockquote::before {
        position: absolute;
        font-size: 50px;
        text-shadow: 0 0 3px $black;
        content: open-quote;
        top: 50%;
        left: 5px;
        font-family: Arial;
    }
    p, ol {
        line-height: 30px;
        text-align: justify;
        font-size: 15px;
    }
    ol {
        list-style: none;
        counter-reset: list;
    }
    li {
        counter-increment: list;
    }
    li::before {
        content: counter(list);
        background-image: $grad-green-blue;
        padding: 0 5px;
        border-radius: 5px;
        color: $white;
        font-weight: bold;
        margin-right: 5px;
    }      
    p.featured-link {
        margin: 10px 0;
        padding-left: 10px;
        font-size: 14px;
        border-left: 5px solid $light-gray;
        a {
            color: $white;
            background-image: $grad-red-orange;
            padding: 2px 10px;
            border-radius: 5px;
        }
    }
}

/* Nama Penulis dan Tanggal Posting */
.post-attribute {
    margin-bottom: 10px;
    font-size: 13px;
    align-items: center;
}
.author-post {
    margin-right: 10px;
    .author-post a {
        color: $black;
    }
}
.date-post {
    margin-right: 10px;
}
#post {
    .post-attribute {
        > img {
            width: 30px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }
}
/*-- Nama Penulis dan Tanggal Posting --*/

/* Share on Social Media */
.share {
    a {
        line-height: 10px;
        margin: 0 3px;
        padding: 10px;
        width: 35px;
        border-radius: 50%;
        display: inline-block;
        background-image: $grad-green-blue;
        color: $white;
    }
}
/*-- Share on Social Media --*/

/* Author Page */
#author {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .author-desc {
        width: 30%;
        > img {
            width: 70%;
            border-radius: 50%;
        }    
    }
    .author-about {
        width: 70%;
        text-align: left;
        line-height: 30px;
    }
    h3 {
        color: $light-gray;
    }
    p {
        margin-bottom: 10px;
    }
}
/*-- Author Page --*/

/*-- Postingan --*/

/* Pagination */
.page-link {
    background-image: $grad-red-orange;
    margin: 0 2px;
    padding: 10px 15px;
    color: $white;
    font-size: 15px;
}
.page-link:hover {
    box-shadow: 0 0 5px $white;
    color: $white;
}
/*-- Pagination --*/
/*-- Article Contents --*/

/* Footer Contents */
footer {
    background-color: $white;
    text-align: center;
    li, a {
        display: inline-block;
    }
    li {
        font-size: 14px;
    }
    a {
        color: $black;
        padding: 20px;
    }
    .credit {
        font-size: 13px;
        padding: 20px;
        a {
            padding: 0;
        }
    }
}
/*-- Footer Contents --*/

@media screen and (max-width: 990px) {
    /* Mobile Navigation */
    nav {
        background-image: $grad-green-blue;
        z-index: 10;
        position: sticky;
        font-size: 20px;
        top: 0;
    }
    /*-- Mobile Navigation --*/

    /* Social-Media Button */
    .social-btn {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: fixed;
        bottom: 0;
        width: 100%;
        z-index: 10;
        a {
            flex-grow: 1;
            text-align: center;
            padding: 10px;
            color: $white;
            background-image: $grad-red-orange;
            font-size: 20px;
        }
    }
    /*-- Social-Media Button --*/

    /* Article Contents */
    article {
        position: relative;
    }
    /*-- Article Contents --*/

    #author {
        flex-direction: column;
        .author-desc, .author-about {
            width: 100%;
        }
        .author-about {
            text-align: center;
            margin-top: 20px;
        }
    }    
}